function Addtask() {
    const [requestT, setrequestT] = useState('');
    const [budget, setbudget] = useState('');
    const [Duration, setDuration] = useState('');
    const [Location, setLocation] = useState('');
    const [Solution, setSolution] = useState('');
    const [Tolerance, setTolerance] = useState('');
    const [MinR, setMinR] = useState('');


    return (
        <div className='Addtask'>

            <div className='Addtask_container'>
                <h1>Enter task details</h1>
                <form>
                    <h2>Type of requested task</h2>
                    <input type='text' id='requestT' value={requestT} onChange={e => setrequestT(e.target.value)} />
                    <h2>Duration</h2>
                    <input type='text' id='Duration' value={Duration} onChange={e => setDuration(e.target.value)} />
                    <h2>Deposit</h2>
                    <input type='text' id='budget' value={budget} onChange={e => setbudget(e.target.value)} />
                    <h2>Location </h2>
                    <input type='text' id='Location' value={Location} onChange={e => setLocation(e.target.value)} />
                    <h2>No. Of Solutions</h2>
                    <input type='text' id='Solution' value={Solution} onChange={e => setSolution(e.target.value)} />
                    <h2>Tolerance </h2>
                    <input type='text' id='Tolerance' value={Tolerance} onChange={e => setTolerance(e.target.value)} />
                    <h2>Minimum reputation</h2>
                    <input type='text' id='MinR' value={MinR} onChange={e => setMinR(e.target.value)} />
                    <button type='submit' onClick={Submit} className='Addtask_submitButton'>Submit</button>

                </form>

            </div>
        </div>
    )
}

export default Addtask
