import React, { useState } from "react";
import Requester from "./Welcome_Req.svg"
import { Link } from "react-router-dom";
import "./RequesterHome.css";
import { UMC, TMC, machine } from "./Addresses"
import Web3 from "web3";
import { ContractABI } from "./ContractABI";
import { ContractABI2 } from "./ContractABI2"


var account;
var contaddress11 = UMC;
var contaddress22 = TMC;
var x;

const web3 = new Web3(new Web3.providers.HttpProvider(machine));

function RequesterHome() {



    const rep = async (e) => {
        e.preventDefault(); //so it won't keep refreshing

        web3.eth.getAccounts(async function (err, acc) {
            if (err != null) {
                alert("There was an error fetching your accounts");
                return;
            }
            if (acc.length > 0) {
                console.log(web3.currentProvider);
                console.log(acc);
                var x = window.prompt("Enter your account prefix: ");
                web3.eth.defaultAccount = acc[x];
                account = acc[x];


                const SenseChainUsers = new web3.eth.Contract(
                    ContractABI, UMC
                )



                const gas = await SenseChainUsers.methods.userReputation(account).estimateGas(); // The error is the contract object is not having an address, something to do with re-arrangement d
                console.log("Gas used: " + gas);
                const result = await SenseChainUsers.methods.userReputation(account).call({ from: account, gas })
                    .then(function (result) {
                        if (result != -1) {
                            alert("My reputation is: " + result);
                        }
                    });
            }
        })
    }

    const view = async (e) => {

        e.preventDefault(); //so it won't keep refreshing

        web3.eth.getAccounts(async function (err, acc) {
            if (err != null) {
                alert("There was an error fetching your accounts");
                return;
            }
            if (acc.length > 0) {
                console.log(web3.currentProvider);
                console.log(acc);
                var x = window.prompt("Enter your account prefix: ");
                web3.eth.defaultAccount = acc[x];
                account = acc[x];

                const SenseChainTasks = new web3.eth.Contract(
                    ContractABI2, TMC
                )

                const gas = await SenseChainTasks.methods.getPendingTasksUser(account).estimateGas(); // The error is the contract object is not having an address, something to do with re-arrangement d
                console.log("Gas used: " + gas);
                const result = await SenseChainTasks.methods.getPendingTasksUser(account).call({ from: account, gas })
                    .then(function (result) {
                        alert("My Requested Tasks: " + result);
                    });
            }
        })


    }
    return (
        <div className="main1">
            <img
                className="home_pic"
                src={Requester}
            />
            <div className="Description">
                <h2 className="Description1">Go to <Link to="/mytasks">Edit Tasks</Link> to delete your tasks</h2>
                <h2 className="Description2">Go to <Link to="/addtask">Add new task</Link> to add a task</h2>
                <h2 className="Description3">To view your reputaion: <form><button type='submit' onClick={rep} className="RequesterButton1">Reputation</button></form></h2>
                <h2 className="Description4">To view the number of tasks you requested: <form><button type='submit' onClick={view} className="RequesterButton2 ">No. of tasks</button></form></h2>
            </div>
        </div>
    )
}

export default RequesterHome