export const initialState = {
    basket: [], //number of current tasks
    basket2: [],
    arrayTask: [],
};

const reducer = (state, action) => {
    console.log(action);
    switch (action.type) {
        case "APPLY":
            return {
                ...state,
                basket: [...state.basket, action.item],
            };

        case "SUBMIT":
            return {
                ...state,
                basket2: [...state.basket2, action.item],
            };

        case "CANCEL":
            const index = state.basket.findIndex(
                (basketItem) => basketItem.id === action.id
            );
            let newBasket = [...state.basket];

            if (index >= 0) { //if the task was found -> remove it
                newBasket.splice(index, 1);

            } else {
                console.warn(
                    `Cant remove task (id: ${action.id}) as its not in the list!`
                )
            }

            return {
                ...state,
                basket: newBasket //change basket to the new basket with the task removed
            }
        default:
            return state;

    }
};

export default reducer;