import React, { useState } from "react";
import Worker from "./Welcome_Requester_2_.svg";
import { Link } from "react-router-dom";
import "./WorkerHome.css";
import { UMC, TMC, machine } from "./Addresses"
import Web3 from "web3";
import { ContractABI } from "./ContractABI"
import { ContractABI2 } from "./ContractABI2"

var account;
var contaddress11 = UMC;
var contaddress22 = TMC;
var x;

const web3 = new Web3(new Web3.providers.HttpProvider(machine));



function WorkerHome() {


    const rep = async (e) => {
        e.preventDefault(); //so it won't keep refreshing

        web3.eth.getAccounts(async function (err, acc) {
            if (err != null) {
                alert("There was an error fetching your accounts");
                return;
            }
            if (acc.length > 0) {
                console.log(web3.currentProvider);
                console.log(acc);
                var x = window.prompt("Enter your account prefix: ");
                web3.eth.defaultAccount = acc[x];
                account = acc[x];


                const SenseChainUsers = new web3.eth.Contract(
                    ContractABI, UMC
                )



                const gas = await SenseChainUsers.methods.userReputation(account).estimateGas(); // The error is the contract object is not having an address, something to do with re-arrangement d
                console.log("Gas used: " + gas);
                const result = await SenseChainUsers.methods.userReputation(account).call({ from: account, gas })
                    .then(function (result) {
                        if (result != -1) {
                            alert("My reputation is: " + result);
                        }
                    });
            }
        })
    }


    return (
        <div>
            <img
                className="home_pic1"
                src={Worker}
            />
            <div className="Description">
                <h2 className="Description1">Go to <Link to="/Findtasks">Find Tasks</Link> to reserve a task</h2>
                <h2 className="Description2">Go to <Link to="/currenttasks">Current Tasks</Link> to view your tasks</h2>
                <h2 className="Description3">To view your reputaion: <form><button type="submit" onClick={rep} className="WorkerButton1">Reputation</button></form></h2>
            </div>
        </div>
    )
}

export default WorkerHome