import React from 'react'
import { useStateValue } from './StateProvider';
import "./TasksAdded.css";
import values from "./ViewTask"
import { newval, change } from "./Addtask2"
import Web3 from "web3";
import { ContractABI2 } from "./ContractABI2"
import { ContractABI } from "./ContractABI"
import { ContractABI3 } from "./ContractABI3"
import { UMC, TMC, machine } from "./Addresses"


var account;

const test = async e => {
    const web3 = new Web3(new Web3.providers.HttpProvider(machine));
    web3.eth.getAccounts(function (err, acc) {
        if (err != null) {
            alert("There was an error fetching your accounts");
            return;
        }
        if (acc.length > 0) {
            console.log(web3.currentProvider);
            console.log(acc);
            web3.eth.defaultAccount = acc[0];
            account = acc[0];
        }
    })

    const SenseChainTasks = new web3.eth.Contract(
        ContractABI2, TMC
    )
    alert("getting tasks!!!");

    const gas1 = await SenseChainTasks.methods.getPendingTasksNumber().estimateGas();
    console.log("Gas used: " + gas1);
    alert(gas1 + " gas added");
    const result = await SenseChainTasks.methods.getPendingTasksNumber().call({ from: account, gas1 })
        .then(async function (result) {
            var j = result;
            var count = 0;
            if (j == 0)
                alert("no tasks");
            for (var i = 0; i < j; i++) {
                const result1 = await SenseChainTasks.methods.getPendingTasks(i).call({ from: account, gas: 1500000 })
                    .then(async function (result1) {
                        console.log(result1);
                        count++;

                        // contract address
                        var tr = document.createElement("tr");
                        var td = document.createElement("td");
                        td.setAttribute("name", "address");
                        td.setAttribute("value", result1[0]);
                        var txt = document.createTextNode(result1[0]);
                        td.appendChild(txt);
                        tr.appendChild(td);
                        values[0] = result1[0];


                        // data type
                        txt = document.createTextNode(result1[4]);
                        var td2 = document.createElement("td");
                        td2.appendChild(txt);
                        tr.appendChild(td2);
                        values[1] = result1[4];

                        // deposit
                        txt = document.createTextNode(result1[6]); //result[6]/1000000
                        var td3 = document.createElement("td");
                        td3.appendChild(txt);
                        tr.appendChild(td3);
                        values[2] = result1[6];

                        // min payment
                        txt = document.createTextNode(result1[6] / (result1[8] * 2)); //result1[6] / (result1[8] * 2 * 1000000
                        var td10 = document.createElement("td");
                        td10.appendChild(txt);
                        tr.appendChild(td10);
                        values[3] = (result1[6] / result1[8] * 2);
                        // duration
                        txt = document.createTextNode(result1[5]);
                        var td4 = document.createElement("td");
                        td4.appendChild(txt);
                        tr.appendChild(td4);
                        values[4] = result1[5];
                        // location
                        txt = document.createTextNode(result1[7]);
                        var td5 = document.createElement("td");
                        td5.setAttribute("name", "loc");
                        var test = result1[7].substring(0, 10); //.substring(0, 10)
                        console.log("test" + test);
                        var ArrayData = test.split(',').map(Number);
                        console.log(ArrayData);
                        values[5] = result1[7];

                        //string with payment location and address of task
                        var xx = ArrayData[0] + "," + ArrayData[1] + "," + result1[0] + "," + result1[8] + "," + (result1[6] / 1000000000000);
                        td5.setAttribute("value", xx);
                        console.log(xx)
                        td5.appendChild(txt);
                        tr.appendChild(td5);
                        // solutions required
                        txt = document.createTextNode(result1[8]);
                        var td6 = document.createElement("td");
                        td6.setAttribute("id", result1[0] + "sol");
                        td6.appendChild(txt);
                        tr.appendChild(td6);
                        values[6] = result1[8];
                        // similarity tol
                        txt = document.createTextNode(result1[9]);
                        var td7 = document.createElement("td");
                        td7.appendChild(txt);
                        tr.appendChild(td7);
                        values[7] = result1[9];
                        // requester
                        var td11 = document.createElement("td");
                        txt = document.createTextNode(result1[1]);
                        td11.appendChild(txt);
                        tr.appendChild(td11);
                        values[8] = result1[1];
                        // requester rep
                        txt = document.createTextNode(result1[2]);
                        var td8 = document.createElement("td");
                        td8.appendChild(txt);
                        tr.appendChild(td8);
                        values[9] = result1[2];
                        // input data
                        var input = document.createElement("input");
                        var td12 = document.createElement("td");
                        input.setAttribute("id", "data" + result1[0]);
                        td12.appendChild(input);
                        tr.appendChild(td12);
                        // reserve button
                        var reserveslot = document.createElement("Button");
                        txt = document.createTextNode("Reserve");
                        reserveslot.setAttribute("onClick", "reserveSlot(this.id)");
                        reserveslot.appendChild(txt);
                        reserveslot.setAttribute("id", "res" + result1[0]);
                        var td15 = document.createElement("td");
                        td15.appendChild(reserveslot);
                        tr.appendChild(td15);

                        // reserve button
                        var reservecheck = document.createElement("Button");
                        txt = document.createTextNode("ReserveCheck");
                        reservecheck.setAttribute("onClick", "reserveCheck(this.id)");
                        reservecheck.appendChild(txt);
                        reservecheck.setAttribute("id", "resck" + result1[0]);
                        var td20 = document.createElement("td");
                        td20.appendChild(reservecheck);
                        tr.appendChild(td20);
                        // submit solution button
                        var button = document.createElement("Button");
                        txt = document.createTextNode("Submit Solution");
                        button.setAttribute("onClick", "submitSolution(this.id)");
                        button.appendChild(txt);
                        button.setAttribute("id", result1[0]);

                        var td9 = document.createElement("td");
                        td9.appendChild(button);
                        tr.appendChild(td9);

                        // min rep 
                        txt = document.createTextNode(result1[10]);
                        var td13 = document.createElement("td");
                        td13.appendChild(txt);
                        tr.appendChild(td13);
                        //document.getElementById("pending").appendChild(tr);
                        values[10] = result1[10];
                        // if all the tasks are added 
                        if (count == j)
                            alert(count + " " + j + "done"); // ends if count 

                    });
            }
        });

    alert("Getting something ...");
    for (var i = 0; i < values.length; i++) {
        alert(i + ": " + values[i]);
    }
    alert("Check console log!");
}
function TasksAdded({ id, title, payment, duration, location, solutions, reputation }) {
    const [{ basket }, dispatch] = useStateValue();
    console.log("this is the basket", basket);
    test();

    const addToBasket = () => {
        // dispatch the task into the data layer
        dispatch({
            type: 'APPLY',
            item: {
                id: id,
                title: title,
                payment: payment,
            }
        })
    }

    return <div className="TasksAdded">
        <div className="Task_info">

            <div className="contract_address">
                <strong>Address:</strong>
                <p>{id}</p>
            </div>
            <div className="data_type">
                <strong>Type:</strong>
                <p>{title}</p>
            </div>
            <div className="deposit">
                <strong>Deposit:</strong>
                <p>{payment}</p>
            </div>
            <div className="duration">
                <strong>Duration:</strong>
                <p>{duration}</p>
            </div>
            <div className="location">
                <strong>Location:</strong>
                <p>{location}</p>
            </div>
            <div className="solutions">
                <strong>Solutions required:</strong>
                <p>{solutions}</p>
            </div>
            <div className="reputation">
                <strong>Reputation:</strong>
                <p>{reputation}</p>
            </div>
            {/*<p className='Task_payment'>
                <strong>{payment}</strong>
                <small> AED</small>
            </p>*/}
        </div>

        <button onClick={test}>View</button>
        <button onClick={addToBasket}>APPLY</button>
    </div>

}

export default TasksAdded
