import React from 'react'
import Web3 from "web3";
import { ContractABI2 } from "./ContractABI2"
import { ContractABI } from "./ContractABI"
import { ContractABI3 } from "./ContractABI3"
import { UMC, TMC, machine } from "./Addresses"
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps";

var account;
var contaddress11 = UMC;
var contaddress22 = TMC;
var values = [];
const web3 = new Web3(new Web3.providers.HttpProvider(machine));
web3.eth.getAccounts(function (err, acc) {
    if (err != null) {
        alert("There was an error fetching your accounts");
        return;
    }
    if (acc.length > 0) {
        console.log(web3.currentProvider);
        console.log(acc);
        web3.eth.defaultAccount = acc[0];
        account = acc[0];
    }
});


const Submit = async e => {

    e.preventDefault();

    const SenseChainTasks = new web3.eth.Contract(
        ContractABI2, contaddress22
    )

    const gas = await SenseChainTasks.methods.getPendingTasksNumber().estimateGas();
    console.log("Gas used: " + gas);
    alert(gas + " gas added");
    const result = await SenseChainTasks.methods.getPendingTasksNumber().call({ from: account, gas })
        .then(async function (result) {
            var j = result;
            var count = 0;
            if (j == 0)
                alert("no tasks");
            for (var i = 0; i < j; i++) {
                const result1 = await SenseChainTasks.methods.getPendingTasks(i).call({ from: account, gas: 1500000 })
                    .then(function (result1) {
                        console.log(result1);

                        // contract address
                        var tr = document.createElement("tr");
                        var td = document.createElement("td");
                        td.setAttribute("name", "address");
                        td.setAttribute("value", result1[0]);
                        var txt = document.createTextNode(result1[0]);
                        td.appendChild(txt);
                        tr.appendChild(td);
                        values[0] = result1[0];


                        // data type
                        txt = document.createTextNode(result1[4]);
                        var td2 = document.createElement("td");
                        td2.appendChild(txt);
                        tr.appendChild(td2);
                        values[1] = result1[4];

                        // deposit
                        txt = document.createTextNode(result1[6]); //result[6]/1000000
                        var td3 = document.createElement("td");
                        td3.appendChild(txt);
                        tr.appendChild(td3);
                        values[2] = result1[6];

                        // min payment
                        txt = document.createTextNode(result1[6] / (result1[8] * 2)); //result1[6] / (result1[8] * 2 * 1000000
                        var td10 = document.createElement("td");
                        td10.appendChild(txt);
                        tr.appendChild(td10);
                        values[3] = (result1[6] / result1[8] * 2);
                        // duration
                        txt = document.createTextNode(result1[5]);
                        var td4 = document.createElement("td");
                        td4.appendChild(txt);
                        tr.appendChild(td4);
                        values[4] = result1[5];
                        // location
                        txt = document.createTextNode(result1[7]);
                        var td5 = document.createElement("td");
                        td5.setAttribute("name", "loc");
                        var test = result1[7]; //.substring(0, 10)
                        console.log("test" + test);
                        var ArrayData = test.split(',').map(Number); //if statement here to compare the !! play around here
                        console.log(ArrayData);
                        values[5] = result1[7];

                        //string with payment location and address of task
                        var xx = ArrayData[0] + "," + ArrayData[1] + "," + result1[0] + "," + result1[8] + "," + (result1[6] / 1000000000000);
                        td5.setAttribute("value", xx);
                        console.log(xx)
                        td5.appendChild(txt);
                        tr.appendChild(td5);
                        // solutions required
                        txt = document.createTextNode(result1[8]);
                        var td6 = document.createElement("td");
                        td6.setAttribute("id", result1[0] + "sol");
                        td6.appendChild(txt);
                        tr.appendChild(td6);
                        values[6] = result1[8];
                        // similarity tol
                        txt = document.createTextNode(result1[9]);
                        var td7 = document.createElement("td");
                        td7.appendChild(txt);
                        tr.appendChild(td7);
                        values[7] = result1[9];
                        // requester
                        var td11 = document.createElement("td");
                        txt = document.createTextNode(result1[1]);
                        td11.appendChild(txt);
                        tr.appendChild(td11);
                        values[8] = result1[1];
                        // requester rep
                        txt = document.createTextNode(result1[2]);
                        var td8 = document.createElement("td");
                        td8.appendChild(txt);
                        tr.appendChild(td8);
                        values[9] = result1[2];

                        // min rep 
                        txt = document.createTextNode(result1[10]);
                        var td13 = document.createElement("td");
                        td13.appendChild(txt);
                        tr.appendChild(td13);
                        document.getElementById("pending").appendChild(tr);
                        values[10] = result1[10];

                        var deletebtn = document.createElement("Button"); // Creates a button visible in the screen
                        txt = document.createTextNode("Delete Task");
                        deletebtn.appendChild(txt);
                        deletebtn.setAttribute("id", "del" + result1[0]);
                        deletebtn.onclick = function () { DeleteTasks(this.id) };
                        var td20 = document.createElement("td");
                        td20.appendChild(deletebtn);
                        tr.appendChild(td20);


                        // if all the tasks are added 
                        if (count == j)
                            alert(count + " " + j + "done"); // ends if count 

                    });

            }
        });


}

function DeleteTasks(id) {

    web3.eth.getAccounts(function (err, acc) {
        if (err != null) {
            alert("There was an error fetching your accounts");
            return;
        }
        if (acc.length > 0) {
            var x = document.getElementById("account").value;
            console.log(web3.currentProvider);
            console.log(acc);
            web3.eth.defaultAccount = acc[x];
            account = acc[x];

            alert(account);


            var rescont = id.split("l");
            console.log("to delete " + rescont);

            // const SenseChainTaskDetailedContract = new web3.eth.Contract(
            //     ContractABI3, rescont[1]
            // )

            const SenseChainTasks = new web3.eth.Contract(
                ContractABI2, contaddress22
            )

            if (account == values[8]) {
                //alert("checked");
                const result = SenseChainTasks.methods.cancelTask(account, rescont[1]).send({ from: account, gas: 1500000 })
                    .then(function (result) {
                        alert("function has been deleted succesfully");
                    })

            }
            else {
                alert("Not able to delete this task, either you are not the owner or task has no solutions");
            }

        }
    })
}



