import "./Home.css";
import React, { useState } from 'react';
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";
import Email2 from './Login';
import Map from "./Map";
import GoogleApiWrapper from "./Map"


function Home() {

    const history = useHistory(); //To change URLs 
    const name = sessionStorage.getItem("Email");

    const requester = e => {
        e.preventDefault() //so it won't keep refreshing
        if (name == "") {
            history.push('/login')
        }
        else {
            history.push('/requester')
        }

    }
    const worker = e => {
        e.preventDefault() //so it won't keep refreshing
        if (name == "") {
            history.push('/login')
        }
        else {
            history.push('/worker')
        }
    }

    return (
        <div className="home">
            <div className="home_container">
                <h2 id='a' className="current_title">Welcome {name}</h2>
                <h2 className="User">What are you today?</h2>
                <button onClick={requester} className='requesterButton'>Requester</button>
                <button onClick={worker} className='workerButton'>Worker</button>
                <h2 className="location">Your current location:</h2>
                <Map className="map"
                />;

            </div>
        </div>
    );
}

export default Home
